import React, { useState, useEffect, useCallback, useMemo } from 'react';
import {
  Box,
  Typography,
  Button,
  Card,
  CardContent,
  CardActions,
  CircularProgress,
  Divider,
  Avatar,
  Chip,
  Paper,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
} from '@mui/material';
import {
  Check as CheckIcon,
  Close as CloseIcon,
  AccessTime as AccessTimeIcon,
  PersonAdd as PersonAddIcon,
  Person as PersonIcon,
  Refresh as RefreshIcon,
  PersonRemove as PersonRemoveIcon,
  HourglassEmpty as HourglassEmptyIcon,
} from '@mui/icons-material';
import { formatDistanceToNow } from 'date-fns';
import { zhCN } from 'date-fns/locale';
import { emergencyCallInvitationService, cancelOrRemoveInvitation, InviteStatus } from '@app/model';

// 定义请求处理器类型
type RequestHandler<T = any> = {
  onSuccess: (res: T) => void;
  onError: (err: Error) => void;
};

// 定义邀请接口
interface IInvitation {
  invitation_id: string;
  user_info: {
    user_id: string;
    display_name: string;
    avatar_url?: string;
  };
  message?: string;
  status: InviteStatus;
  expires_at: number;
  created_at: string;
}

// 定义 API 响应类型
interface IApiResponse<T = any> {
  shellIsOk: boolean;
  dataIsOk: boolean;
  data?: {
    is_ok: boolean;
    msg?: string;
    sent_invitations?: IInvitation[];
    received_invitations?: IInvitation[];
  } & T;
}

// 获取状态标签的配置
const getStatusConfig = (status: InviteStatus, expiresAt: number) => {
  const isExpired = new Date(expiresAt * 1000) < new Date();
  const statusTexts: Record<InviteStatus, string> = {
    pending: '等待处理',
    accepted: '已接受',
    declined: '已拒绝',
    expired: '已过期',
    cancelled: '已取消',
  };

  const statusColors: Record<InviteStatus, 'default' | 'primary' | 'success' | 'error' | 'warning'> = {
    pending: 'primary',
    accepted: 'success',
    declined: 'error',
    expired: 'default',
    cancelled: 'default',
  };

  return {
    text: statusTexts[status] || status,
    color: statusColors[status] || 'default',
    isExpired,
    showExpiry: status === 'pending' && !isExpired,
  };
};

// 邀请状态标签组件
function InvitationStatusChip({ status, expiresAt }: { status: InviteStatus; expiresAt: number }) {
  const config = getStatusConfig(status, expiresAt);

  return (
    <Box display="flex" alignItems="center" gap={1}>
      <Chip size="small" label={config.text} color={config.color} variant="outlined" />
      {config.showExpiry && (
        <Chip
          size="small"
          icon={<AccessTimeIcon fontSize="small" />}
          label={`${formatDistanceToNow(expiresAt * 1000, {
            addSuffix: true,
            locale: zhCN,
          })}`}
          color="primary"
          variant="outlined"
        />
      )}
    </Box>
  );
}

// 用户信息卡片组件
function UserInfoCard({
  userInfo,
  message,
  status,
  expiresAt,
  isReceived = false,
  onRespond,
  isProcessing = false,
}: {
  userInfo: { user_id: string; display_name: string; avatar_url?: string };
  message?: string;
  status: string;
  expiresAt: number;
  isReceived?: boolean;
  onRespond?: (action: 'accept' | 'decline' | 'cancel' | 'remove') => Promise<void>;
  isProcessing?: boolean;
}) {
  const [isProcessingAction, setIsProcessingAction] = useState(false);

  const handleRespond = async (action: 'accept' | 'decline' | 'cancel' | 'remove') => {
    if (!onRespond) return;

    try {
      setIsProcessingAction(true);
      await onRespond(action);
    } finally {
      setIsProcessingAction(false);
    }
  };

  return (
    <Card variant="outlined" sx={{ mb: 2, opacity: isProcessing ? 0.7 : 1 }}>
      <CardContent>
        <Box display="flex" alignItems="center" mb={1}>
          <Avatar src={userInfo.avatar_url} alt={userInfo.display_name} sx={{ width: 40, height: 40, mr: 2 }}>
            {userInfo.display_name?.[0]?.toUpperCase() || '?'}
          </Avatar>
          <Box flexGrow={1}>
            <Typography variant="subtitle1">{userInfo.display_name || '未知用户'}</Typography>
            <Typography variant="body2" color="text.secondary">
              ID: {userInfo.user_id}
            </Typography>
          </Box>
          <InvitationStatusChip status={status} expiresAt={expiresAt} />
        </Box>

        {message && (
          <Typography variant="body2" color="text.secondary" sx={{ mt: 1, mb: 1 }}>
            {message}
          </Typography>
        )}
      </CardContent>

      {isReceived ? (
        // 接收的邀请
        <CardActions sx={{ justifyContent: 'flex-end', pt: 0 }}>
          {status === 'pending' ? (
            // 待处理的邀请：显示接受/拒绝按钮
            <>
              <Button
                size="small"
                color="error"
                startIcon={<CloseIcon />}
                onClick={() => handleRespond('decline')}
                disabled={isProcessing || isProcessingAction}>
                拒绝
              </Button>
              <Button
                size="small"
                color="primary"
                variant="contained"
                startIcon={<CheckIcon />}
                onClick={() => handleRespond('accept')}
                disabled={isProcessing || isProcessingAction}>
                接受
              </Button>
            </>
          ) : status === 'accepted' ? (
            // 已接受的邀请：显示解除关系按钮
            <Button
              size="small"
              color="error"
              variant="outlined"
              startIcon={<PersonIcon />}
              onClick={() => handleRespond('remove')}
              disabled={isProcessing || isProcessingAction}>
              解除关系
            </Button>
          ) : null}
        </CardActions>
      ) : status === 'pending' || status === 'accepted' ? (
        // 发送的邀请：显示取消或解除关系按钮
        <CardActions sx={{ justifyContent: 'flex-end', pt: 0 }}>
          <Button
            size="small"
            color="error"
            variant="outlined"
            startIcon={<CloseIcon />}
            onClick={() => handleRespond('cancel')}
            disabled={isProcessing || isProcessingAction}>
            {status === 'pending' ? '取消邀请' : '解除关系'}
          </Button>
        </CardActions>
      ) : null}
    </Card>
  );
}

const InvitationManager: React.FC = () => {
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [isRefreshing, setIsRefreshing] = useState<boolean>(false);
  const [invitations, setInvitations] = useState<{
    sent: IInvitation[];
    received: IInvitation[];
  }>({ sent: [], received: [] });
  const [isProcessing, setIsProcessing] = useState<boolean>(false);

  // 加载邀请列表
  const loadInvitations = useCallback(async (showLoading = true) => {
    if (showLoading) {
      setIsLoading(true);
    } else {
      setIsRefreshing(true);
    }
    setError(null);

    try {
      const res = await emergencyCallInvitationService.listAllInvitations({
        params: {},
      } as any);
      
      if (res.shellIsOk && res.dataIsOk && res.data?.is_ok) {
        setInvitations({
          sent: (res.data.sent_invitations || []).map((invite: any) => ({
            ...invite,
            status: invite.status as InviteStatus,
          })),
          received: (res.data.received_invitations || []).map((invite: any) => ({
            ...invite,
            status: invite.status as InviteStatus,
          })),
        });
      } else {
        setError(res.data?.msg || '加载邀请列表失败');
      }
    } catch (err) {
      console.error('加载邀请列表异常:', err);
      setError('发生未知错误');
    } finally {
      setIsLoading(false);
      setIsRefreshing(false);
    }
  }, []);

  // 处理取消或删除邀请
  const handleCancelOrRemove = useCallback(
    async (invitationId: string, isReceived: boolean) => {
      try {
        setIsProcessing(true);

        const res = await cancelOrRemoveInvitation(invitationId, isReceived);
        if (!res.shellIsOk || !res.dataIsOk || !res.data?.is_ok) {
          throw new Error(res.data?.msg || '操作失败');
        }
        // 直接更新状态
        setInvitations(prev => ({
          sent: prev.sent.filter(i => i.invitation_id !== invitationId),
          received: prev.received.filter(i => i.invitation_id !== invitationId),
        }));
      } catch (err) {
        console.error('处理邀请失败:', err);
        setError(err instanceof Error ? err.message : '操作失败，请重试');
      } finally {
        setIsProcessing(false);
      }
    },
    [],
  );

  // 响应邀请
  const handleRespondToInvite = useCallback(
    async (invitationId: string, action: 'accept' | 'decline' | 'cancel' | 'remove'): Promise<void> => {
      try {
        setIsProcessing(true);

        if (action === 'accept' || action === 'decline') {
          // 接受或拒绝邀请
          const res = await emergencyCallInvitationService.respondToInvite({
            params: { invitation_id: invitationId, action },
          } as any);

          if (!res.shellIsOk || !res.dataIsOk || !res.data?.is_ok) {
            throw new Error(res.data?.msg || '操作失败');
          }
          
          // 直接更新状态
          setInvitations(prev => {
            const updateInvitation = (invites: IInvitation[]) =>
              invites.map(invite =>
                invite.invitation_id === invitationId
                  ? {
                      ...invite,
                      status: action === 'accept' ? 'accepted' : ('declined' as InviteStatus),
                    }
                  : invite,
              );

            return {
              sent: updateInvitation(prev.sent),
              received: updateInvitation(prev.received),
            };
          });
        } else {
          // 取消邀请或解除关系
          const isReceived = action === 'remove';
          await handleCancelOrRemove(invitationId, isReceived);
        }
      } catch (err) {
        console.error('处理邀请失败:', err);
        setError(err instanceof Error ? err.message : '操作失败，请重试');
      } finally {
        setIsProcessing(false);
      }
    },
    [handleCancelOrRemove],
  );

  // 处理刷新
  const handleRefresh = useCallback(() => {
    loadInvitations(false);
  }, [loadInvitations]);

  // 组件加载时获取数据
  useEffect(() => {
    loadInvitations();
  }, [loadInvitations]);

  const renderContent = () => {
    if (isLoading) {
      return (
        <Box display="flex" justifyContent="center" p={4}>
          <CircularProgress />
        </Box>
      );
    }

    if (error) {
      return (
        <Box p={2}>
          <Typography color="error">{error}</Typography>
          <Button variant="outlined" color="primary" onClick={() => loadInvitations()} startIcon={<RefreshIcon />}>
            重试
          </Button>
        </Box>
      );
    }

    const hasSentInvitations = invitations.sent.length > 0;
    const hasReceivedInvitations = invitations.received.length > 0;

    return (
      <Box>
        <Typography variant="h6" gutterBottom>
          收到的邀请
        </Typography>
        {hasReceivedInvitations ? (
          <Box mb={4}>
            {invitations.received.map(invite => (
              <UserInfoCard
                key={`received-${invite.invitation_id}`}
                userInfo={invite.user_info}
                message={invite.message}
                status={invite.status as InviteStatus}
                expiresAt={invite.expires_at}
                isReceived={true}
                onRespond={async action => {
                  if (action === 'remove') {
                    await handleCancelOrRemove(invite.invitation_id, true);
                  } else if (action === 'cancel') {
                    await handleCancelOrRemove(invite.invitation_id, false);
                  } else {
                    await handleRespondToInvite(invite.invitation_id, action);
                  }
                }}
                isProcessing={isRefreshing || isProcessing}
              />
            ))}
          </Box>
        ) : (
          <Box
            display="flex"
            flexDirection="column"
            alignItems="center"
            justifyContent="center"
            p={4}
            textAlign="center"
            bgcolor="action.hover"
            borderRadius={1}
            mb={4}>
            <PersonIcon color="disabled" sx={{ fontSize: 48, mb: 1 }} />
            <Typography color="textSecondary">暂无收到的邀请</Typography>
          </Box>
        )}

        <Typography variant="h6" gutterBottom>
          已发送的邀请
        </Typography>
        {hasSentInvitations ? (
          <Box>
            {invitations.sent.map(invite => (
              <UserInfoCard
                key={`sent-${invite.invitation_id}`}
                userInfo={invite.user_info}
                message={invite.message}
                status={invite.status as InviteStatus}
                expiresAt={invite.expires_at}
                isReceived={false}
                onRespond={async action => {
                  if (action === 'cancel') {
                    await handleCancelOrRemove(invite.invitation_id, false);
                  }
                }}
                isProcessing={isRefreshing || isProcessing}
              />
            ))}
          </Box>
        ) : (
          <Box
            display="flex"
            flexDirection="column"
            alignItems="center"
            justifyContent="center"
            p={4}
            textAlign="center"
            bgcolor="action.hover"
            borderRadius={1}>
            <PersonIcon color="disabled" sx={{ fontSize: 48, mb: 1 }} />
            <Typography color="textSecondary">暂无已发送的邀请</Typography>
          </Box>
        )}
      </Box>
    );
  };

  return (
    <Box p={2}>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
        <Typography variant="h5">紧急联系人邀请</Typography>
        <Button
          variant="outlined"
          size="small"
          onClick={handleRefresh}
          disabled={isRefreshing}
          startIcon={isRefreshing ? <CircularProgress size={20} /> : <RefreshIcon />}>
          {isRefreshing ? '刷新中...' : '刷新'}
        </Button>
      </Box>
      {renderContent()}
    </Box>
  );
};

export default InvitationManager;
